@use '../../scss/abstracts';

.wp-block-md-storyful-fse-full-sample-dynamic {
    color: abstracts.$primary-theme-color;
}

.leadership__header {
    padding: 39px 15px;
    text-align: center;
    color: #fffdfa;
    background-color: #ff4c15
}

.leadership__header-heading {
    margin: 0;
    font-size: 55px;
    font-weight: 700;
    line-height: 61px;
    letter-spacing: -.01em;
    font-family: arial,helvetica,sans-serif
}

.leadership__header-heading strong {
    color: transparent;
    -webkit-text-stroke: 1px #fffdfa
}

.leadership__author__box {
    display: flex;
    flex-wrap: wrap;
    margin: 72px 0 0
}

.leadership__author__box-item {
    flex-basis: 33.33%;
    margin: 0 0 94px;
    padding: 0 30px;
    cursor: pointer
}

.leadership__author__box-item-inner {
    text-align: center
}

.leadership__author__box-item-inner-img {
    margin: 0 auto 16px;
    position: relative;
    width: 224px
}

.leadership__author__box-item-inner-img .author-img {
    width: 224px;
    height: 224px;
    border-radius: 100%;
    box-shadow: 0 4px 10px 2px rgba(167,169,172,.5);
    -o-object-fit: cover;
    object-fit: cover
}

.leadership__author__box-item-inner-img .linked-in-icon {
    position: absolute;
    width: 42px;
    height: 42px;
    top: 72%;
    right: 0;
    background: #fffdfa;
    border-radius: 100%
}

.leadership__author__box-item-inner-img .linked-in-icon img {
    width: 100%;
    height: 100%
}

.leadership__author__box-item-inner-content .leadershipName {
    font-size: 24px;
    font-weight: 700;
    line-height: 29px;
    font-family: arial,helvetica,sans-serif;
    color: #11152c;
    text-transform: capitalize;
    margin: 0 0 12px
}

.leadership__author__box-item-inner-content .leadershipTitle {
    font-size: 20px;
    font-weight: 400;
    line-height: 24px;
    font-family: arial,helvetica,sans-serif;
    color: #11152c;
    text-transform: capitalize;
    margin: 0
}

.leadership__popup-model {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    overflow: hidden;
    outline: 0;
    width: 100%;
    background-color: #fffdfa;
    display: none;
    align-items: center;
    cursor: auto
}

.leadership__popup-model-content {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    background: #fff;
    position: relative;
    z-index: 9;
    overflow: auto;
    overflow-x: hidden
}

.leadership__popup-model-body {
    max-width: 956px;
    padding: 0 15px;
    width: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    display: flex;
    justify-content: space-between
}

.leadership__popup-model-author-details-main {
    flex-basis: 24.2%;
    text-align: center
}

.leadership__popup-model-author-details-main-img-section {
    position: relative;
    margin: 0 auto 16px;
    width: 224px
}

.leadership__popup-model-author-details-main-img-section .author-img {
    border-radius: 100%;
    width: 224px;
    height: 224px;
    -o-object-fit: cover;
    object-fit: cover
}

.leadership__popup-model-author-details-main-img-section .linked-in-icon {
    position: absolute;
    width: 42px;
    height: 42px;
    top: 70%;
    right: 0;
    background: #fffdfa;
    border-radius: 100%
}

.leadership__popup-model-author-details-main-img-section .linked-in-icon img {
    width: 100%;
    height: 100%
}

.leadership__popup-model-author-details-main .leadershipName {
    font-size: 24px;
    font-weight: 700;
    line-height: 29px;
    font-family: arial,helvetica,sans-serif;
    color: #11152c;
    text-transform: capitalize;
    margin: 0 0 12px
}

.leadership__popup-model-author-details-main .leadershipTitle {
    font-size: 20px;
    font-weight: 400;
    line-height: 24px;
    font-family: arial,helvetica,sans-serif;
    color: #11152c;
    text-transform: capitalize;
    margin: 0
}

.leadership__popup-model-about-author-box {
    flex-basis: 60%
}

.leadership__popup-model-about-author-box .about-author {
    border-bottom: 1px solid rgba(17,21,44,.5);
    padding: 0 0 35px;
    margin: 0 0 25px;
    position: relative;
    font-size: 20px;
    font-weight: 700;
    line-height: 26px;
    letter-spacing: -.01em;
    font-family: arial,helvetica,sans-serif;
    color: #11152c;
    text-transform: uppercase
}

.leadership__popup-model-about-author-box .about-author:hover:before {
    width: 44px
}

.leadership__popup-model-about-author-box .about-author:before {
    content: "";
    width: 34px;
    height: 9px;
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: #ff4c15;
    transition: all .3s ease-in-out
}

.leadership__popup-model-about-author-box .leadershipBio {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: #11152c;
    margin: 0 0 30px
}

.leadership__popup-model-header {
    position: relative
}

.leadership__popup-model-header button {
    position: absolute;
    top: 100px;
    right: 100px;
    background: transparent;
    border: none;
    width: auto;
    height: auto;
    font-family: arial,helvetica,sans-serif;
    z-index: 999;
    cursor: pointer
}

.leadership__popup-model-header button span {
    font-size: 20px;
    font-weight: 400;
    line-height: 26px;
    color: #11152c;
    letter-spacing: -.01em;
    text-transform: uppercase
}

.leadership__popup-model.open-popup {
    display: flex
}

.leadership__join-team {
    border-top: 1px solid #000;
    padding: 40px 0 50px;
    text-align: center;
    font-family: arial,helvetica,sans-serif
}

.leadership__join-team__heading {
    font-size: 28px;
    font-weight: 400;
    line-height: 31px;
    letter-spacing: -.01em
}

.leadership__join-team__heading-span {
    font-weight: 700;
    font-size: 28px;
    line-height: 31px;
    letter-spacing: -.01em;
    display: block;
    margin: 0 0 50px
}